import React, { useState } from 'react';
import './App.css'

export default function App() {
	const questions = [
		{
			questionText: 'What is the capital of France?',
			answerOptions: [
				{ answerText: 'New York', isCorrect: false },
				{ answerText: 'London', isCorrect: false },
				{ answerText: 'Paris', isCorrect: true },
				{ answerText: 'Dublin', isCorrect: false },
			],
		},
		{
			questionText: 'Who is CEO of Tesla?',
			answerOptions: [
				{ answerText: 'Jeff Bezos', isCorrect: false },
				{ answerText: 'Elon Musk', isCorrect: true },
				{ answerText: 'Bill Gates', isCorrect: false },
				{ answerText: 'Tony Stark', isCorrect: false },
			],
		},
		{
			questionText: 'The iPhone was created by which company?',
			answerOptions: [
				{ answerText: 'Apple', isCorrect: true },
				{ answerText: 'Intel', isCorrect: false },
				{ answerText: 'Amazon', isCorrect: false },
				{ answerText: 'Microsoft', isCorrect: false },
			],
		},
		{
			questionText: 'How many Harry Potter books are there?',
			answerOptions: [
				{ answerText: '1', isCorrect: false },
				{ answerText: '4', isCorrect: false },
				{ answerText: '6', isCorrect: false },
				{ answerText: '7', isCorrect: true },
			],
		},
  ];
  
  const handleButtonClick =(choice)=>{
     if(choice){
       setScore(score+1)
     }
     const newQuestion=questionCount+1
     if(newQuestion< questions.length){
       setQuestionCount(newQuestion)
     }
     else{
       setShowScore(true)
     }
  }
  const [score,setScore] = useState(0)
  const [showScore,setShowScore] =useState(false)
  const[questionCount,setQuestionCount] =useState(0)

   function resetQuiz(){
    setScore(0)
    setShowScore(false)
    setQuestionCount(0)
  }


	return (
		<div className='app'>
			
			{showScore ? (
        <div className="score-container">
				<div className='score-section'>{`You scored ${score} out of ${questions.length}`}</div>
        <button className="reset" onClick={resetQuiz}>reset</button>
        </div>
			) : (
				<>
					<div className='question-section'>
						<div className='question-count'>
							<span>{`Question ${questionCount+1}`}</span>/{questions.length}
						</div>
						<div className='question-text'>{questions[questionCount].questionText}</div>
					</div>
					<div className='answer-section'>
            {questions[questionCount].answerOptions.map(answer =>(
              <button  onClick={()=>{handleButtonClick(answer.isCorrect)}}>{answer.answerText}</button>
            ))}
      
					
					</div>
				</>
			)}
		</div>
  );
      }